import React from 'react';
import { forwardRefWrapper } from '../../libs/forward-ref-wrapper';
import { IntrinsicElementProps } from '../../types/utility';

type SpacingPreset =
  | 'space-0.5'
  | 'space-1'
  | 'space-1.5'
  | 'space-2'
  | 'space-3'
  | 'space-4'
  | 'space-5'
  | 'space-6'
  | 'space-8'
  | 'space-10';

interface SpacerProps extends IntrinsicElementProps<'div'> {
  size?: string | number;
  orientation?: 'horizontal' | 'vertical';
  inline?: boolean;
  spacing?: SpacingPreset;
}

const spacingPreset = {
  'space-0.5': '4px',
  'space-1': '8px',
  'space-1.5': '12px',
  'space-2': '16px',
  'space-3': '24px',
  'space-4': '32px',
  'space-5': '40px',
  'space-6': '48px',
  'space-8': '64px',
  'space-10': '80px',
};

export const Spacer = /* @__PURE__ */ forwardRefWrapper<HTMLDivElement, SpacerProps>(
  'Spacer',

  {
    size: 0,
    orientation: 'horizontal',
  },

  (props, externalRef) => {
    const { size, orientation, inline, spacing, style: otherStyle, ...otherProps } = props;

    const spacingSize = spacing ? spacingPreset[spacing] : size;

    const horizontalStyle = { display: inline ? 'inline-block' : 'block', height: '100%', width: spacingSize };
    const verticalStyle = { display: inline ? 'inline-block' : 'block', width: '100%', height: spacingSize };
    const style = orientation === 'vertical' ? verticalStyle : horizontalStyle;

    return <div aria-hidden="true" style={{ ...style, ...otherStyle }} {...otherProps} ref={externalRef} />;
  },
);
